.yt__main {
  background-color: rgb(57, 57, 57);
  border: 2px solid black;
  padding: 10px;
  color: white;
}
.yt__top {
  display: flex;
  justify-content: space-between;
}

.yt__top_item:first-child {
  margin-left: 30px;
}

.yt__top_item_right {
  display: flex;
  align-items: center;
}

.ty__lte {
  position: relative;
  top: -2px;
  margin-left: 5px;
  margin-right: 5px;
}

.yt__top_2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.yt__top_2 > div {
  /* border: 1px solid red; */
}

.ty__title {
  font-size: 25px;
  font-weight: bold;
}

.yt__top_2_right {
  /* background-color: red; */
}
.yt__top_2_right > span {
  margin-left: 5px;
  margin-right: 5px;
}

.ty__top_2_profile {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: orangered;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  font-size: 10px;
}
